
.welcome img{
  margin-top: 70px;
  margin-bottom: 20px;
  width: 100px;
}
.welcome #bottom{
  position: absolute;
  bottom: 0px;
  width: 100%;
  height: 60px;
  background-color: cornflowerblue;
}
.welcome #bottom a{
  display: inline-block;
  line-height: 60px;
  width: 48%;
  height: 100%;
  text-align: center;
  color: #fff;
}
.welcome .note{
  width: 100%;
  height:80px;
  font-size: 30px;
}
.welcome .copyright{
  margin-top: 100px;
  width: 100%;
  height:40px;
  margin-bottom: 20px;
}



