
.info{
  position: fixed;
  left: 0px;
  top: 0px;
  z-index: 50;
  width: 100%;
  height: 100%;
  background: #fff;
  transition: all 0.2s linear;
}
.info .back{
  position: absolute;
  width: 40px;
  height: 20px;
  top: 10px;
  left: 10px;
  font-size: 20px;
}
.move-enter-active, .move-leave-active {
  transform: translate3d(0, 0, 0);
}
.move-enter, .move-leave-to {
  transform: translate3d(100%, 0, 0);
}
.info h3{
  margin-top: 100px;
}
.info h3 span{
  color: green;
}



