
.main{
  width: 100%;
  height: 100%;
}
.mapDisplay{
  width: 100%;
  height: 100%;
}
.tab{
  position: fixed;
  top: 0px;
  z-index: 30;
  width: 100%;
  height: 40px;
  background-color: cornflowerblue;
}
.tab img{
  position: absolute;
  display: inline-block;
  top: 5px;
  left: 5px;
  width: 25px;
}
.tab .title{
  margin: 0 auto;
  margin-top: 10px;
  width: 100px;
  color: #fff;
  font-family: "Microsoft YaHei UI";
}
.tab .more{
  position: absolute;
  display: inline-block;
  top: -13px;
  right: 5px;
  font-size: 40px;
  color: #fff;
}
.detail{
  position: absolute;
  right: 5px;
  width: 170px;
  background: #fff;
}
.detail .myInfo,.detail .exit{
  padding-left: 25px;
  height: 40px;
  line-height: 40px;
  text-align: left;
}
.detail .myInfo:active,.detail .exit:active{
  background: #2f83c7;
  color: #fff;
}
.baiduMap{
  height: 100%;
}
.baiduMap .anchorBL{
  display:none;
}
.uploadTrackPoint{
  position: fixed;
  bottom: 10px;
  right: 10px;
  width: 50px;
  height: 40px;
}
.select{
  position: fixed;
  bottom: 10px;
  right: 90px;
  width: 50px;
  height: 40px;
}





