
.register {
  position: fixed;
  left: 0px;
  top: 0px;
  z-index: 30;
  width: 100%;
  height: 100%;
  background: #fff;
  transition: all 0.2s linear;
}
.Lmove-enter-active, .Lmove-leave-active {
  transform: translate3d(0, 0, 0);
}
.Lmove-enter, .Lmove-leave-to {
  transform: translate3d(100%, 0, 0);
}
.register .back {
  position: absolute;
  width: 40px;
  height: 20px;
  top: 10px;
  left: 10px;
  font-size: 20px;
}
.register-wrap {
  margin-top: 140px;
  text-align: center;
  height: 100%;
}
.rs{
  height: 150%;
}
h3{
  font-size: 40px;
  margin-bottom: 110px;
}
input {
  display: block;
  margin: 0 auto;
  margin-bottom: 20px;
  width: 250px;
  height: 40px;
  line-height: 40px;
  outline: none;
  border: 1px solid #888;
  padding: 10px;
  box-sizing: border-box;
}
p {
  color: red;
}
button {
  display: block;
  width: 250px;
  height: 40px;
  line-height: 40px;
  margin: 0 auto;
  border: none;
  background-color: cornflowerblue;
  color: #fff;
  font-size: 16px;
  margin-bottom: 5px;
}
button:active{
  background-color: green;
}
span {
  cursor: pointer;
}
span:hover {
  color: cornflowerblue;
}



